body {
    font-family: 'Ubuntu', Helvetica;
}

a {
    color: black;
    text-decoration: none;
}

.main-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    opacity: 0;
    z-index: 99999;
    -webkit-transition: opacity 400ms ease-in;
    -moz-transition: opacity 400ms ease-in;
    transition: opacity 400ms ease-in;
    pointer-events: none;
}

.main-container:target {
    opacity: 1;
    pointer-events: auto;
}

.main-container>div {
    width: 300px;
    position: relative;
    margin: 10% auto;
    padding: 20px;
    border-radius: 10px;
    background: #fff;
    background: -moz-linear-gradient(#fff, #999);
    background: -webkit-linear-gradient(#fff, #999);
    background: -o-linear-gradient(#fff, #999);
}

.close {
    font-family: Helvetica;
    line-height: 25px;
    color: #CCC;
    position: absolute;
    right: 3px;
    text-align: center;
    font-weight: bold;
    top: 1px;
    width: 24px;
    text-decoration: none;
}

.close:hover {
    font-family: Helvetica;
    line-height: 25px;
    color: #AAA;
    position: absolute;
    right: 3px;
    text-align: center;
    font-weight: bold;
    top: 1px;
    width: 24px;
    text-decoration: none;
}

.dialog-container {
    display: flex;
    width: 70%;
}

.post-it-container {
    width: 30%;
}

.list-container {
    margin-left: 5px;
    margin-top: 5px;
    padding-left: 5px;
}

ul {
    margin-top: 5px;
    padding-left: 5px;
}

button, a.button-link {
    font-family: 'Ubuntu';
    display: block;
    box-shadow: inset 0px 1px 0px 0px #ffffff;
    background: linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
    border-radius: 5px;
    border: 1px solid #dcdcdc;
    cursor: pointer;
    color: #666666;
    font-size: 13px;
    padding: 9px 20px;
    margin-top: 5px;
    margin-left: 40px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffffff;
    width: 100px;
}

a.button-link {
    width: 50px;
}

button:hover, a.button-link:hover {
    background: #ffffff;
}

input {
    border-radius: 5px;
    border: 1px solid #dcdcdc;
    box-shadow: inset 0px 1px 0px 0px #ffffff;
    margin-top: 5px;
    height: 2em;
    margin-left: 5px;
    padding-left: 5px;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown-content a {
    padding: 12px 16px;
    display: block;
}

#yellow:hover {
    background: -webkit-linear-gradient(#fff, #f9f063);
}

#pink:hover {
    background: -webkit-linear-gradient(#fff, #ffc7e0);
}

#green:hover {
    background: -webkit-linear-gradient(#fff, #afe132);
}

#blue:hover {
    background: -webkit-linear-gradient(#fff, #64dcff);
}

.dropdown:hover .dropdown-content {
    display: block;
}
